import FileItem from "jx/editor/editionitems/FileItem";
export default class ImageItem extends FileItem {
    constructor(dataObject) {
        dataObject.data.asset.type = "Image";
        super(Object.assign({
            icoSymbol: "picture-o",
            actions: ["download", "preview"],
            informations: ["asset"],
            backgroundColor: "#d5eaff"
        }, dataObject));
    }
}
